body,
html {
  font-family: Campton, Arial, Helvetica, sans-serif;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(
    to top,
    #597196,
    #6a83ac,
    #7c96c3,
    #8ea9da,
    #a1bdf2
  );
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  //filter: grayscale(0.5);
}

button,
input[type="submit"],
input[type="reset"] {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

input {
  text-align: center;
  caret-color: #566585;
}

input::placeholder {
  font-size: 30px;
  color: #0b0f14;
}

a {
  text-decoration: none;
}
